<template>
  <div ref="img" class="image-view">
    <img ref="image" :src="file" alt="" width="100%" style="object-fit: fill;" @click="view" />
    <div v-if="loadFail" @click.stop.prevent="init">加载失败，点击重新加载</div>
  </div>
</template>

<script>
export default {
  name: "imageView",
  props: {
    fileId: {
      type: String,
      required: true
    },
    fileExt: {
      type: String,
      required: true
    },
    index: {
      type: Number,
      default: 1
    },
    fileTotal: {
      type: Number,
      default: 0
    },
    autoLoad: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isLoad: false,
      loading: false,
      loadFail: false,
      file: null
    };
  },
  mounted() {
    if (this.autoLoad) this.init();
  },
  methods: {
    init() {
      if (this.loading || this.isLoad) return;
      this.loading = true;
      this.$mainHttp.resourse
        .viewFile({
          mid: this.fileId,
          suffix: this.fileExt,
          index: this.index
        })
        .then(res => {
          let byteArray = new Uint8Array(res);
          let binary = "";
          for (let i = 0; i < byteArray.byteLength; i++) {
            binary += String.fromCharCode(byteArray[i]);
          }
          switch (this.fileExt) {
            case "png":
              this.file = "data:image/png;base64," + window.btoa(binary);
              break;
            case "gif":
              this.file = "data:image/gif;base64," + window.btoa(binary);
              break;
            case "x-icon":
              this.file = "data:image/x-icon;base64," + window.btoa(binary);
              break;
            default:
              this.file = "data:image/jpeg;base64," + window.btoa(binary);
              break;
          }
          this.loading = false;
          this.isLoad = true;
          setTimeout(() => {
            if (!this.loadFail) this.$emit("finish", this.index);
          });
        })
        .catch(() => {
          this.loading = false;
          if (!this.loadFail) this.$emit("finish", this.index);
          this.loadFail = true;
        });
    },
    view() {
      if (!this.loadFail) {
        this.$emit("preview", this.file);
      }
    }
  }
};
</script>

<style lang="less" scoped>
.image-view {
  width: 100%;
  position: relative;

  .remark {
    text-align: center;
  }
}
</style>



