<template>
  <div ref="img" class="video-view">
    <t-video :src="mediaSrc" :poster="mediaPoster"></t-video>
  </div>
</template>

<script>
import tVideo from "@/components/r-video";
import defaultBg from "@/assets/video/video.png";

export default {
  name: "VideoView",
  components: { tVideo },
  props: {
    fileId: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      mediaPoster: defaultBg,

      isLoad: false,
      loading: false,
      loadFail: false,
      file: null
    };
  },
  computed: {
    mediaSrc() {
      return this.$mainHttp.resourse.file(this.fileId);
    }
  },
  mounted() {},
  methods: {}
};
</script>

<style lang="less" scoped>
.video-view {
  background-color: #000;
}
</style>



