<template>
  <div ref="main" id="file-preview" class="file-preview" @scroll="lazyLoad">
    <div ref="head" class="head-sticky">
      <span v-if="fileExt !== 'txt' && fileExt !== 'mp4'">{{fileTotal}}-{{active}}</span>
    </div>
    <div class="file-main">
      <div v-if="fileExt === 'txt'" class="file-txt">
        <h2>{{fullName}}</h2>
        <p>
          {{txtContent}}
        </p>
      </div>
      <div v-else-if="fileExt === 'mp4'">
        <video-view :file-id="fileId"></video-view>
        <h2>{{fullName}}</h2>
      </div>
      <div v-else-if="canOpenFileExt.indexOf(fileExt) > -1 " ref="content">
        <image-view :file-id="fileId" :file-ext="fileExt" :file-total="1" :index="1" auto-load
          @preview="imageView" />
        <h2>{{fullName}}</h2>
      </div>
      <div v-else ref="content">
        <h2>{{fullName}}</h2>
        <image-view class="lazy" :ref="'img'+ index" v-for="index in viewTotal" :key="index"
          :file-id="fileId" :file-ext="fileExt" :file-total="fileTotal" :index="index"
          @finish="handleFinish" @preview="toView(index)" />

        <van-image-preview v-model="viewShow" :start-position="startIndex" :images="viewImages"
          :loop="false" @change="onChange">
          <template v-slot:index>第{{ viewIndex }}页，共{{fileTotal}}页</template>
        </van-image-preview>
      </div>
    </div>
  </div>
</template>

<script>
import { ImagePreview } from "vant";

import ImageView from "./ImageView";
import VideoView from "./VideoView";

// import PinchZoom from "pinch-zoom-js";
export default {
  name: "r-file-view",
  components: {
    ImageView,
    VideoView,
    [ImagePreview.Component.name]: ImagePreview.Component
  },
  props: {
    fileId: {
      type: String,
      required: true
    },
    fileName: {
      type: String,
      required: true
    },
    fileExt: {
      type: String,
      required: true
    },
    fileTotal: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      canOpenFileExt: ["txt", "bmp", "gif", "jpg", "png", "ico"],
      txtContent: "",
      loadIndex: 1,
      finishedHeight: 0, // 加载完图片的高度
      canLoad: true, // 可否继续加载
      active: 1,

      // stickyHeight: 0,
      // stickyWidth: 0,
      // stickyLeft: 0,
      // stickyStyle: {},
      // isSticky: false,

      viewTotal: 1,
      allFinished: false,

      // 多张图片预览形式
      startIndex: 0,
      viewIndex: 1,
      viewShow: false,
      viewImages: []
    };
  },
  computed: {
    fullName() {
      return this.fileName + "." + this.fileExt;
    }
  },
  mounted() {
    // this.stickyHeight = this.$refs["head"].offsetTop;
    // this.stickyWidth = this.$refs["head"].clientWidth;
    // this.stickyLeft = this.$refs["head"].offsetLeft;
  },
  methods: {
    load() {
      this.init();
      // 滚屏函数
      window.addEventListener("scroll", this.lazyLoad);
      const divDom = document.getElementById("file-preview");
      if (divDom) divDom.addEventListener("scroll", this.lazyLoad);
    },
    init() {
      if (this.fileExt === "txt") {
        this.$mainHttp.resourse
          .viewTxt({
            mid: this.fileId
          })
          .then(res => {
            if (res.Result) {
              this.txtContent = res.Data;
            } else {
              this.txtContent = "加载失败";
            }
          })
          .catch(() => {
            this.txtContent = "加载异常";
          });
      } else if (
        this.canOpenFileExt.findIndex(r => r === this.fileExt) === -1
      ) {
        this.active = 0;
        // this.viewImages.length = this.fileTotal;
        this.viewImages = []; //
        for (let i = 0; i < this.fileTotal; i++) {
          this.viewImages.push("");
        }
        this.$nextTick(() => {
          this.lazyLoad();

          // const el = this.$refs["content"];
          // new PinchZoom(el, {
          //   draggableUnzoomed: false,
          //   minZoom: 1,
          //   onZoomStart: function(object, event) {
          //     // Do something on zoom start
          //     // You can use any Pinchzoom method by calling object.method()
          //   },
          //   onZoomEnd: function(object, event) {
          //     // Do something on zoom end
          //   }
          // });
        });
      }
    },
    lazyLoad() {
      if (this.fileTotal < 1) {
        return;
      }
      this.scrollPage();
      if (!this.allFinished) {
        setTimeout(() => {
          this.loadImage(this.loadIndex);
        }, 200);
      }
    },
    // 加载页码
    scrollPage() {
      // 获取所有锚点元素
      const navContents = document.querySelectorAll(".lazy");
      // 所有锚点元素的 offsetTop
      const offsetTopArr = [];
      navContents.forEach(item => {
        offsetTopArr.push(item.offsetTop);
      });
      // 获取当前文档流的 scrollTop
      const scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      const divDom = document.getElementById("file-preview");
      const divScrollTop = divDom.scrollTop;

      // 定义当前点亮的导航下标
      let navIndex = 0;
      for (let n = 0; n < offsetTopArr.length; n++) {
        // 如果 scrollTop 大于等于第 n 个元素的 offsetTop 则说明 n-1 的内容已经完全不可见
        // 那么此时导航索引就应该是 n 了
        if (scrollTop >= offsetTopArr[n] && scrollTop > 0) {
          navIndex = n + 1;
        } else if (divScrollTop >= offsetTopArr[n] && divScrollTop > 0) {
          navIndex = n + 1;
        } else {
          break;
        }
      }
      // 把下标赋值给 vue 的 data
      this.active = navIndex >= this.fileTotal ? this.fileTotal : navIndex + 1;
    },
    loadImage(index) {
      if (index > this.fileTotal) return;
      let imgDiv = this.$refs["img" + index][0] || this.$refs["img" + index];
      if (!imgDiv) return;
      if (!this.canLoad) return;

      if (!imgDiv.isLoad) {
        // 可视区域高度
        const h = window.innerHeight;
        // 滚动区域高度
        const s = document.documentElement.scrollTop || document.body.scrollTop;
        const scrollTop = document.getElementById("file-preview").scrollTop;

        const navContents = document.querySelectorAll(".lazy");
        const finishedHeight =
          navContents.length > 0
            ? navContents[navContents.length - 1].offsetTop
            : 0;
        // console.log(index + ":", h, s, scrollTop, this.finishedHeight, finishedHeight)
        const outHeight =
          h + s + 100 >= this.finishedHeight || h + s + 100 >= finishedHeight;
        const domOut =
          h + scrollTop + 100 >= this.finishedHeight ||
          h + scrollTop + 100 >= finishedHeight;
        // console.log(outHeight, domOut);
        if (outHeight || domOut) {
          this.viewTotal++;
          this.canLoad = false;

          imgDiv.init();
          if (index >= this.fileTotal) {
            this.allFinished = true;
          }
        }
      }
    },
    handleFinish(index) {
      this.loadIndex++;
      this.canLoad = true;
      let offsetHeight = this.$refs["img" + index][0].$el.offsetHeight;
      this.finishedHeight += offsetHeight;
      this.viewImages.splice(index - 1, 1, this.$refs["img" + index][0].file);
      this.lazyLoad();
    },
    // 单图预览
    imageView(imageSrc) {
      this.imagePreview = ImagePreview({
        images: [imageSrc],
        showIndex: false,
        onClose() {}
      });
    },
    // 开启多图预览
    toView(index) {
      this.startIndex = index - 1;
      this.onChange(index - 1);
      this.viewShow = true;
    },
    // 切换下一张图，暂不允许倒叙看（从末尾看起需优化）
    onChange(index) {
      const lastIndex = this.viewIndex;
      this.viewIndex = index + 1;
      // 下一张
      if (index === lastIndex) {
        const nextIndex = this.viewIndex + 1;
        if (!this.viewImages[nextIndex] && nextIndex <= this.fileTotal) {
          const imgDiv =
            this.$refs["img" + nextIndex][0] || this.$refs["img" + nextIndex];
          this.viewTotal++;
          this.canLoad = false;

          imgDiv.init();
          if (index >= this.fileTotal) {
            this.allFinished = true;
          }
        }
      } else {
        // 上一张
      }
    }
  },
  watch: {
    fileTotal: {
      immediate: true,
      handler() {
        this.load();
      }
    }
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.lazyLoad);
    const divDom = document.getElementById("file-preview");
    if (divDom) divDom.removeEventListener("scroll", this.lazyLoad);

    if (this.imagePreview) this.imagePreview.close();
  }
};
</script>

<style lang="less" scoped>
@import url("./../../styles/common/base.less");
.file-preview {
  overflow-y: auto;

  .head-sticky {
    position: fixed;
    top: 0;
    right: 16px;
    z-index: 999;
    font-size: @font-title;
    line-height: 44px;
    color: #fff;
  }

  .file-main {
    min-height: calc(100vh - 45px);
    word-break: break-all;
    h2 {
      padding: 2vw 0;
      font-size: @font-title;
    }
    .file-txt {
      padding: 10px 16px;
      font-size: @font-title;
      text-align: left;
      line-height: 1.5;
    }
  }
}
</style>



