<template>
  <div class="file-view">
    <r-file-view :file-id="fileId" :file-name="fileName" :file-ext="fileExt"
      :file-total="fileTotal">
    </r-file-view>
  </div>
</template>
<script>
import RFileView from "@/components/r-file-view";

export default {
  name: "r-file-preview",
  components: { RFileView },
  props: {
    fileName: {
      type: String,
      required: true
    },
    fileExt: {
      type: String,
      default: ""
    },
    fileId: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      canOpenFileExt: ["txt", "bmp", "gif", "jpg", "png", "ico", "mp4"],
      // fileInfo: this.$store.getters.cloudFile,
      fileTotal: 1
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (!this.canOpenFileExt.find(r => r === this.fileExt)) {
        this.$mainHttp.material
          .fileConvertCount({
            mid: this.fileId
          })
          .then(res => {
            if (res.Result) {
              this.fileTotal = res.Total;
            } else if (res.Info) {
              this.$toast(res.Info);
              this.fileTotal = 0;
            } else {
              this.fileTotal = 0;
            }
          })
          .catch(() => {
            this.fileTotal = 0;
          });
      }
    }
  }
};
</script>

<style lang="less" scoped>
.file-view {
  min-height: calc(100% - 44px);
  background-color: #fcfcfc;
}
</style>



